import Vue from "vue";
import config, { saveConfig } from "../config-loader";
export default Vue.extend({
    name: "HomeSettings",
    mounted() {
        this.form = JSON.parse(JSON.stringify(config));
    },
    methods: {
        onSubmit() {
            console.log(this.form);
            saveConfig(this.form);
        }
    },
    data() {
        return {
            form: {
                origin: ""
            }
        };
    },
    components: {}
});
