export async function parallelTask2Map(task, shouldEnd, max) {
    const result = await parallelTask(task, shouldEnd, max);
    return result.reduce((p, v) => {
        if (v?.key) {
            p[v.key] = v.value;
        }
        return p;
    }, {});
}
export default function parallelTask(task, shouldEnd, max) {
    let i = 0;
    const all = {};
    let runningCnt = 0;
    return new Promise((resolve, reject) => {
        const ret = [];
        function check() {
            if (shouldEnd(i)) {
                return;
            }
            if (runningCnt > max) {
                return;
            }
            const promise = task(i);
            const index = i;
            all[index] = true;
            i++;
            runningCnt++;
            promise
                .then(result => {
                ret[index] = result;
                delete all[index];
                runningCnt--;
                check();
                if (runningCnt === 0) {
                    resolve(ret);
                }
            })
                .catch(function (e) {
                console.log(e);
                reject(e);
            });
            check();
        }
        check();
    });
}
